<template>
  <div class="van-nav-bar van-nav-bar--fixed van-hairline--bottom" style="z-index: 8;">
    <div class="van-nav-bar__left" @click="onBack">
      <i class="van-icon van-icon-arrow-left van-nav-bar__arrow"></i>
      <span class="van-nav-bar__text">{{backName}}</span>
    </div>
    <div class="van-nav-bar__title van-ellipsis">
      <slot name="title" />
    </div>
    <div class="van-nav-bar__right" >
      <span class="van-button__text nav-bar--info" v-if="right">
        <slot name="right" />
      </span>
      <span class="van-button__text nav-bar--info" @click="onSearch" v-else>
        <van-icon name="search" />
      </span>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    backName: {
      type: String,
      default: 'Back'
    },
  },
  computed: {
    right () {
      return this.$scopedSlots['right']
    }
  },
  methods: {
    onBack() {
      this.$router.back();
    },
    onSearch() {
      this.$router.push('/search');
    }
  }
};
</script>

<style lang="scss">
.search{
  z-index: 1;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
}
.search .van-search{
  padding: 6px 12px;
}
.nav-bar--info{
  cursor: pointer;
  color: #1989fa;
}
