<template>
  <div v-if="items && items.length">
    <div class="timeline">
      <ul>
        <template v-for="(item, key) in items">
          <li v-if="key > 0" :key="key">
            <div class="tl-main">
              <h3 @click="loadPage(item)">{{item.title}}</h3>
              
              <p @click="loadPage(item)">
                <img :src="item.pics[0].src" alt="">
              </p>
              <p>{{item.description}}</p>
            </div>
            <div class="tl-time">
              <h4>{{tago(item.time)}}</h4>
            </div>
          </li>
        </template>
      </ul>
    </div>
    <van-popup v-model="showTimeLine" closeable position="bottom" round class="popup-timeline" @close="hideTimeLine" :duration="0.5">
      <div style="margin: -6px 16px 0">
        <van-divider :style="{ borderColor: '#888' }">Chi tiết</van-divider>
      </div>
      <Tin :item="tin" />
    </van-popup>
  </div>
</template>

<script>
import Tin from "@/components/Tin.vue";
import * as timeago from 'timeago.js';
export default {
  props: ["items"],
  components: {
    Tin
  },
  data() {
    return {
      showTimeLine: false,
      tin: {}
    }
  },
  computed: {
    // inCartQuantity() {
    //   return this.product.quantity;
    // }
  },
  methods: {
    tago: function(time) {
      return timeago.format(time)
    },
    loadPage: function (data) {
      let vm = this;
      vm.showTimeLine = true;
      vm.tin = data;
      if(window.history) window.history.pushState("", "", '#/detail/'+data.key);
      else{
        vm.$router.push('#/detail/'+data.key)
      }
    },
    hideTimeLine: function () {
      let vm = this;
      if(window.history) window.history.back();
      else{
        vm.$router.back()
      }
      vm.tin = {}
    }
    // addToCart(id) {
    //   this.$store.dispatch("addToCart", id);
    // }
  },
};
</script>
