import API from "./tin.api";
import Fuse from "fuse.js";
let fuse = null;

function removeUnicodeSpace(str) {
  try {
    return str.replace(/[àáảãạâầấẩẫậăằắẳẵặ]/g, 'a')
      .replace(/[èéẻẽẹêềếểễệ]/g, 'e')
      .replace(/[đ]/g, 'd')
      .replace(/[ìíỉĩị]/g, 'i')
      .replace(/[òóỏõọôồốổỗộơờớởỡợ]/g, 'o')
      .replace(/[ùúủũụưừứửữự]/g, 'u')
      .replace(/[ỳýỷỹỵ]/g, 'y')
      .replace(/[`~!@#$%^&*()_|+\=?;:'",.<>\{\}\[\]\\\/]/gi,"")
      .replace(/ {2,}/g,' ')
      .replace(/ /g,'-');
  } catch {
    return ''
  }
}

//Khởi tạo state mặc định
const initialState = () => ({
  itemsSearch: [],
  items: [],
  itemsCovid: [],
  itemsTC: [],
  //giá trị lưu tạm thời dùng cập nhật dữ liệu mới hoặc chỉnh sửa 
  itemTemp: {}
});

/**
 * State object
 * Use: this.$store.Namemodule.key
 */
const state = initialState();


/**
 * Getter functions
 * Use: this.$store.getters['Namemodule/key']
 */
const getters = {
  itemTemp: state => state.itemTemp,
  items: state => state.items,
  itemsCovid: state => state.itemsCovid,
  itemsTC: state => state.itemsTC,
  itemsSearch: state => state.itemsSearch,
}

/**
 * Mutations
 * this.$store.commit('Namemodule/key', data)
 */
const mutations = {
  SEARCH_SETUP(state) {
    let options = {
      shouldSort: true,
      threshold: 0.7,
      location: 0,
      distance: 100,
      minMatchCharLength: 2,
      keys: [
        "title",
        "description"
      ]
    };
    fuse = new Fuse(state.items, options)
  },
  SEARCH(state, data){
    //extend search join 2 char 'Man 'Old
    let results = fuse.search(data);
    if(results && results.length){
      let output = [];
      for (const it of results) {
        output.push(it.item)
      }
      state.itemsSearch = output
    }
    else{
      state.itemsSearch = []
    }
  },
  SET_ALL2 (state, data) {
    let covid = [];
    let taichinh = [];
    for(let i in data){
      if(data[i].description && 
        (data[i].description.indexOf('covid') !== -1 || data[i].description.indexOf('corona') !== -1 
      || data[i].description.indexOf('bệnh') !== -1 ) || data[i].description.indexOf('benh') !== -1)
      {
        covid.push(data[i])
      }
      else{
        taichinh.push(data[i])
      }
    }
    state.itemsCovid = covid
    state.itemsTC = taichinh
  },
  SET_ALL (state, data) {
    state.items = data
  },
  SET (state, data) {
    state.itemTemp = data
  },
  GET (state, key) {
    state.itemTemp = state.items.find((data) => data.key === key)
    console.log(state.itemTemp )
  },
  ADD (state) {
    state.itemTemp.time = new Date();
    state.items.push(state.itemTemp)
  },
  EDIT (state, data) {
    let items = state.items
    items.splice(items.indexOf(data), 1)
    state.items = items
    state.itemTemp = data
  },
  REMOVE (state, data) {
    let items = state.items
    items.splice(items.indexOf(data), 1)
  },
  CLEAN (state) {
    state.itemTemp = {}
  },

  //cài đặt lại state
  RESET (state) {
    const newState = initialState();
    Object.keys(newState).forEach(key => {
      state[key] = newState[key]
    });
  },
}

/**
 * Actions
 * Use: this.$store.dispatch('Namemodule/key', data)
 */
const actions = {
  setAll: async ({commit}, key) => {
    let data = [];
    if(key == 'tin-hot'){
      data = await API.GET_ALL();
      commit('SET_ALL', data)
    }
    else{
      data = await API.GET_ALL(true);
      commit('SET_ALL2', data)
    }
    // commit('SEARCH_SETUP')
  },
  set: async ({commit}, key) => {
    let data = await API.GET(key);
    commit('SET', data)
  },
  add: async ({commit}) => {
    commit('ADD')
  },
  edit: async ({commit}, data) => {
    commit('EDIT', data)
  },
  remove: async ({commit}, data) => {
    commit('REMOVE', data)
  },
  clean: async ({commit}) => {
    commit('CLEAN')
  },
  reset: async ({ commit }) =>  {
    commit('RESET');
  },
}

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations
}