<template>
  <div class="search-page">
    <NavBar class="search">
      <template slot="title">
        <van-search shape="round" placeholder="Placeholder" v-model="search" @input="onSearch"/>
      </template>
      <template slot="right">Search</template>
    </NavBar>

    <div class="content">
      <div v-if="tins && tins.length" class="row">
        <div class="col-xs-12 col-sm-12 col-md-3">
          
        </div>
        <div class="col-xs-12 col-sm-12 col-md-6">
        <template v-for="(val, key) in tins">
          <van-cell :title="val.title" :label="val.description" :key="key"/>
        </template>
        </div>
        <div class="col-xs-12 col-sm-12 col-md-3">
          
        </div>
      </div>
    </div>
  </div>
</template>

<script>
function debounce (fn, delay) {
  var timeoutID = null
  return function () {
    clearTimeout(timeoutID)
    var args = arguments
    var that = this
    timeoutID = setTimeout(function () {
      fn.apply(that, args)
    }, delay)
  }
}
// @ is an alias to /src
import NavBar from "@/components/NavBar.vue";

export default {
  name: "search",
  components: {
    NavBar
  },
  data() {
    return {
      search: ''
    }
  },
  computed: {
    tins(){
      console.log(this.$store.getters['Tin/itemsSearch'])
      return this.$store.getters['Tin/itemsSearch']
    },
  },
  methods: {
    onSearch: debounce(function(val){
      this.$store.commit('Tin/SEARCH', val)
    },500),
  },
  mounted: function() {
    var vm = this;
    this.$store.dispatch('Tin/setAll', 'tin-hot')
  }
};
</script>
